<template>
  <div>
    <x-header></x-header>
    <group>
      <x-address title="" placeholder="" v-model="carCity" :list="addressData" hide-district></x-address>
      <x-input title="" placeholder="B123456" v-model="carNO" widht="105px"></x-input>
      <datetime title="" v-model="carDate" :min-year=2010 :max-year=2017></datetime>
       <selector title="" placeholder="" v-model="carUse" :options="carUseData"></selector>
      <x-switch title="" v-model="isBusiness"></x-switch>
    </group>

    <p style="padding: 16px 10px;">
      <x-button type="primary" @click.native="submit"></x-button>
    </p>

     <loading v-model="loading" text=""></loading>

  </div>
</template>


<script>
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import Datetime from 'vux/src/components/datetime/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
export default {
  components: {
    Group,
    Cell,
    XInput,
    Selector,
    PopupPicker,
    XSwitch,
    XButton,
    XAddress,
    ChinaAddressData,
    Datetime,
    XHeader,
    Loading
  },
  data () {
    return {
      addressData: ChinaAddressData,
      carCity: [],
      carNO: '',
      isBusiness: true,
      carDate: '2017-01-01',
      carUse: '',
      carUseData: [{key: '-', value: '-'}, {key: '-', value: '-'}, {key: '-', value: '-'}, {key: '-', value: '-'}],
      loading: false
    }
  },
  methods: {
    submit: function () {
      // 
      if (this.carCity.length <= 0) {
        this.$vux.toast.show({
          type: 'warn',
          text: ''
        })
        return
      }
      // 
      if (!this.carNO) {
        this.$vux.toast.show({
          type: 'warn',
          text: ''
        })
        return
      }
      if (!/^([\u4e00-\u9fa5]{1})[a-zA-Z]{0,1}(\d{6})$/.test(this.carNO)) {
        this.$vux.toast.show({
          type: 'warn',
          text: ''
        })
        return
      }
      // 
      if (!this.carDate) {
        this.$vux.toast.show({
          type: 'warn',
          text: ''
        })
        return
      }
      // 
      if (!this.carUse) {
        this.$vux.toast.show({
          type: 'warn',
          text: ''
        })
        return
      }
      let carData = {
        city: this.carCity,
        NO: this.carNO,
        date: this.carDate,
        use: this.carUse,
        isBusiness: this.isBusiness
      }
      this.loading = true
      window.sessionStorage.setItem('myCarData', window.JSON.stringify(carData))
      this.$router.push({ path: '/insure/car/result' })
    }
  }
}
</script>


<style>
  .weui_input,
  .weui_select option {
    text-align: right;
  }
  .weui_cells{
    margin-top: 0 !important;
  }
</style>


